:root {
  --font-primary: "Red Hat Display", sans-serif;
  --scroll-width: 12px;
  --scroll-track-bg: #f1f1f1;
  --scroll-thumb-bg: #888;
  --scroll-thumb-hover-bg: #555;
  --custom-scroll-width: 8px;
  --custom-scroll-track-bg: #e0e0e0;
  --custom-scroll-thumb-bg: #555;
  --custom-scroll-thumb-hover-bg: #333;
  --nav-link-color: #333;
  --nav-link-hover-color: #32CD32;
  --nav-link-active-color: #333;
  --nav-link-active-line-color: #32CD32;
  --animation-slideup-duration: 0.6s;
  --animation-spin-slow-duration: 3s;
  --h2-font-size: 2rem;
  --h2-color: #333;
  --h2-border-color: #FFA500;
}

body {
  font-family: var(--font-primary);
}

::-webkit-scrollbar {
  width: var(--scroll-width);
}

::-webkit-scrollbar-track {
  background: var(--scroll-track-bg);
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  background-color: var(--scroll-thumb-bg);
  border-radius: 10px;
  border: 3px solid var(--scroll-track-bg);
}

::-webkit-scrollbar-thumb:hover {
  background-color: var(--scroll-thumb-hover-bg);
}

.custom-scroll {
  scrollbar-width: thin;
  scrollbar-color: var(--custom-scroll-thumb-bg) var(--custom-scroll-track-bg);
}

.custom-scroll::-webkit-scrollbar {
  width: var(--custom-scroll-width);
}

.custom-scroll::-webkit-scrollbar-track {
  background: var(--custom-scroll-track-bg);
}

.custom-scroll::-webkit-scrollbar-thumb {
  background-color: var(--custom-scroll-thumb-bg);
  border-radius: 8px;
  border: 2px solid var(--custom-scroll-track-bg);
}

.custom-scroll::-webkit-scrollbar-thumb:hover {
  background-color: var(--custom-scroll-thumb-hover-bg);
}

@keyframes slideUp {
  from {
    transform: translateY(30px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.card {
  opacity: 0;
  animation: slideUp var(--animation-slideup-duration) ease forwards;
}

.company-slider {
  position: relative;
  overflow: hidden;
}

.slide-content {
  position: relative;
  text-align: center;
  color: white;
}

.slide-image {
  width: 100%;
  height: 500px;
  object-fit: cover;
  filter: brightness(0.7);
}

.slide-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.7);
}

.nav-link {
  position: relative;
  text-decoration: none;
  color: var(--nav-link-color);
  padding-bottom: 5px;
  transition: color 0.3s ease;
}

.nav-link:hover {
  color: var(--nav-link-hover-color);
}

.nav-link-active {
  position: relative;
  color: var(--nav-link-active-color);
  font-weight: bold;
}

.nav-link-active::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: -5px;
  width: 100%;
  height: 3px;
  background-color: var(--nav-link-active-line-color);
  transition: width 0.3s ease, background-color 0.3s ease;
}

.nav-link-active:hover {
  color: var(--nav-link-hover-color);
}

.nav-link::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: -5px;
  width: 0;
  height: 3px;
  background-color: var(--nav-link-hover-color);
  transition: width 0.3s ease;
}

.nav-link:hover::after {
  width: 100%;
}

@keyframes spin-slow {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.animate-spin-slow {
  animation: spin-slow var(--animation-spin-slow-duration) linear infinite;
}

